/*----------- Footer  ---------------*/
.footer {
  padding: 100px 0;
}

.footer-con {
  width: 80%;
  margin: auto;
  max-width: 1300px;
  gap: 200px;
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}

.footer-left {
  width: 50%;
}
.f-title img{
  width: 100%;
}
.f-description p {
  font-size: 20px;
  color: #FFFFFF;
}

.footer-right {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.footer-right h3 {
  color: #FFFFFF;
  font-size: 30px;
}

.footer-right input {
  padding: 8px 15px;
  width: 300px;
  border-radius: 50px;
  border: none;
  outline: none;
  background: #868585;
  margin-left: -8px;
}

.footer-right input::placeholder {
  color: black;
}

.footer-right a {
  text-decoration: none;
  font-size: 18px;
  color: #FFFFFF;
  margin-top: 10px;
}

.footer-left-img {
  position: absolute;
  left: 0;
  bottom: 0;
}

.footer-left-img img {
  width: 350px;
}

.footer-right-img {
  position: absolute;
  right: 0;
  bottom: 0;
}

.footer-right-img img {
  width: 300px;
}

@media only screen and (max-width:1240px) {
  .footer-con {
    width: 60%;
    margin: auto;
    max-width: 1300px;
    gap: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer-left {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer-right {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (max-width:1000px){
  .footer-left-img img {
    width: 250px;
  }
  .footer-right-img img {
    width: 200px;
  }
}
@media only screen and (max-width:760px){
  .footer-left-img img {
    width: 120px;
  }
  .footer-right-img img {
    width: 100px;
  }
}
@media only screen and (max-width:500px) {
  .footer-con {
    width: 90%;
  }
  .footer-left-img {
    display: none;
  }

  .footer-right-img {
    display: none;
  }

}